import vue from 'vue'
import { bus } from '../main';

export default vue.extend({
  name: 'Nav',
  props:{
    lan: String
  },
  methods: {
    changeLan(tag){
      console.log("Change to: " + tag);
      bus.$emit("ChangeLan", tag);
    },
    changeTopic(v){
      bus.$emit("ChangeTopic", v);
    }
  },
  data(){
    return {
      lan: "EN"
    }
  }
})